.todoTemplate{
	width: 512px;
	margin:6rem auto 0;
	border-radius: 4px;
	overflow: hidden;
	border: 1px solid #1d284e;;
	.todoTemplateTitle{
		background: #1d284e;
		color: #fff;
		height: 4rem;
		font-size: 1.5rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.content{
		background: #fff;
		.total{
			background: #eef1ff;
			text-align: right;
			padding: 15px;
			border-top:1px solid #999;
		}
		.todoList{
			.item{
				border-top:1px solid #999;
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 10px 15px;
				font-size:13px;
				input:checked +{
					span{
						color:#999;
						text-decoration: line-through;
					}
				}
			}
		}
	}
}